<template>
<section>
  <el-tabs class="el-col el-col-24" type="card" v-model="activeName" @tab-click="handelTabChange">
    <el-tab-pane label="未就诊" name="already">
    </el-tab-pane>
    <el-tab-pane label="已就诊" name="not">
    </el-tab-pane>
  </el-tabs>
  <div style="position: relative;">
    <patient-list :active="activeName" ref="patientList"></patient-list>
  </div>
</section>
</template>
<script>
// import {getSelectPatientListTable,getSelectPatientInfoTable} from "../../api"
import patient_list from '@/components/patient_list' // 患者列表
import patient_info from '@/components/patient_info' // 患者详情
export default {
  components:{
    'patient-list': patient_list,
    'patient-info': patient_info
  },
  data(){
    return{
      activeName:'already'
    }
  },
  methods:{
    handelTabChange(tab){
      let treatmentState = tab.index == 0 ? 0 : 1
      this.$refs.patientList.getData({
        treatmentState: treatmentState,
        index: 1 // 切换标签 充值页码
      })
      sessionStorage.setItem('patient_list_active',tab.name)
    }
  },
  mounted(){
    this.activeName = sessionStorage.getItem('patient_list_active')?
    sessionStorage.getItem('patient_list_active'): "already"
    let treatmentState = this.activeName == 'already'?0:1
    this.$refs.patientList.getData({
      treatmentState: treatmentState
    })
  }
}
</script>
<style scoped>
.link1{
  color: #3E85FA;
  cursor: pointer;
}
.info-title{
  text-align: center;
  margin: 20px 0;
  color: #333333;
  font-size: 20px;
  letter-spacing: 2px;
}
.row-bottom{
  margin-top: 24px;
}
.linzheng-list{
  position: relative;
}
.table-top-searchbox{
  position: absolute;
  right: 2px;
  height: 60px;
  width: 360px;
  z-index: 10;
}
</style>




