<template>
<el-row>
  <el-col :span="24">
  <!-- 挂号部分 -->
  <!-- <el-form label-width="100px" :model="formData" :rules="rules"> -->
  <el-form label-width="110px" :model="formData" :rules="rules" ref="form">
    <el-col :span="8" class="huanzhe">            
      <el-form-item label="挂号序号：">
        <el-input v-model="formData.registrationNum" readonly="" disabled=""></el-input>
      </el-form-item>
      <el-form-item label="就诊科室：" prop="deptCode">
          <el-select v-model="formData.deptCode" @change="selectDept" placeholder="请选择就诊科室">
            <el-option
              v-for="item in optionsDeptName"
              :key="item.id"
              :label="item.deptName"
              :value="item.deptCode">
            </el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="性别：">
          <el-select @change="sexChange" v-model="formData.emSex" placeholder="请选择性别">
            <el-option
              v-for="item in optionsSex"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="联系人：">
        <el-input v-model="formData.emComName"></el-input>
      </el-form-item>
      <el-form-item label="费用类别：" prop="emType">
          <el-select v-model="formData.emType" placeholder="请选择费用类别" size="medium">
            <el-option
              v-for="item in costType"
              :key="item.value"
              :label="item.mecidalTypeName"
              :value="item.mecidalTypeCode">
            </el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="孕否：" prop="isYuFu">
          <el-select v-model="formData.isYuFu" placeholder="请选择孕否">
            <el-option
              v-for="item in optionsYuFu"
              :key="item.value"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled">
            </el-option>
          </el-select>
      </el-form-item>
    </el-col>
    <el-col :span="8" class="huanzhe">            
      <el-form-item label="就诊号：" prop="mecidalNum">
        <el-input v-model="formData.mecidalNum"></el-input>
      </el-form-item>
      <el-form-item label="就诊医生：" prop="userCode">
        <el-select v-model="formData.userCode" @focus="funCheckDept"
         @change="funSelectUser" placeholder="请先选择就诊科室">
            <el-option
              v-for="item in userListData"
              :key="item.userCode"
              :label="item.userName"
              :value="item.userCode">
            </el-option>
          </el-select>
        <!-- <el-autocomplete v-model="formData.userName"
          :fetch-suggestions="queryUserList"
          @select="handleSelect"
          style="width: 100%"
          placeholder="请先选择就诊科室"
        ></el-autocomplete> -->
      </el-form-item>
      <el-form-item label="年龄：" prop="emAge">
        <el-input v-model="formData.emAge"></el-input>
      </el-form-item>
      <el-form-item label="联系电话：">
        <el-input v-model="formData.emMobile"></el-input>
      </el-form-item>
      <el-form-item label="体重：">
        <el-input v-model="formData.weight"></el-input>
        <span class="input-fix">kg</span>
      </el-form-item>
      <el-form-item label="添加人：">
        <el-input v-model="formData.maker"></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="8" class="huanzhe">
      <el-form-item label="就诊时间：" prop="onSetDate">
            <el-date-picker
            v-model="formData.onSetDate"
            type="datetime"
            :picker-options="pickerOptions0"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间">
          </el-date-picker>
      </el-form-item>
      <el-form-item label="姓名：" prop="emName">
        <el-input v-model="formData.emName"></el-input>
      </el-form-item>
      <el-form-item label="身份证号：" prop="emNum">
        <el-input v-model="formData.emNum"></el-input>
      </el-form-item>
      <el-form-item label="就诊类别：" prop="mecidalType">
          <el-select v-model="formData.mecidalType" placeholder="请选择就诊类别" size="medium">
            <el-option
              v-for="item in mecidalType"
              :key="item.value"
              :label="item.mecidalTypeName"
              :value="item.mecidalTypeCode">
            </el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="住址：">
        <el-input v-model="formData.emAddress"></el-input>
      </el-form-item>
      <el-form-item label="添加时间：">
          <el-date-picker
          v-model="formData.makeDate"
          type="datetime"
          :picker-options="pickerOptions0"
          value-format="yyyy-MM-dd HH:mm:ss"
          placeholder="选择日期时间 [可选 会自动生成]">
        </el-date-picker>
      </el-form-item>
    </el-col>
  </el-form>
  </el-col>
  <el-col :span="24" style="margin-top: 24px;" align="center">
    <el-button type="primary" @click="handelRegister">挂 号</el-button>
  </el-col>
</el-row>
</template>
<script>
import {dateFmt} from '../util/fun.js'
import { mapState} from 'vuex'
import {
    getBillCode, // 获取挂号序号
    insertPatientInfo, // 添加患者信息 - 挂号
    updatePatientInfo,
    getGyUser, // 医生列表 根据科室
  } from '../api'
export default {
  computed:{
    ...mapState({
      optionsDeptName: state => state.common.keshi, // 科室信息
      mecidalType: state=> state.common.mecidalType, // 就诊类型
      costType: state=> state.common.costType,
    }),
  },
  data(){
    let CheckId = (rule, value, callback) => {
        if (!value) {
          return
        }
        let regIdNo = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
        if(!regIdNo.test(value)){
            callback(new Error('身份证格式错误'));
          }else{
            callback()
          }
    }
    return{
      pickerOptions0: { // 时间 今天之后
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
       formData:{
        //  registrationNum: '',
        //  registrationNumMainID: '',
         deptName: '', // 科室名称 deptName
         deptCode: '', // 科室代码
         userName: '', // 就诊医生
         emName: '', // 患者姓名
         mecidalNum: '', // 就诊号
         isYuFu: 0, // 怀孕 默认否
         emSex: 1, // 性别 默认男
         weight: null,
         maker: sessionStorage.getItem('username'), // 添加人
         makeDate: null, //  添加时间
         emComName: '', // 联系人 可选
         emAge: '', // 年龄 可选
         emMobile: '', // 电话 可选
         weight: '', // 体重 可选
         emAddress: '', // 住址 可选
       },
       rules:{ // 验证规则
        emName:[{required: true, message: '姓名不能为空', trigger: 'blur'}],
        mecidalNum:[{required: true, message: '就诊号不能为空', trigger: 'blur'}],
        deptCode:[{required: true, message: '就诊科室不能为空', trigger: 'change'}],
        userCode:[{required: true, message: '就诊医生不能为空', trigger: 'change'}],
        isYuFu:[{required: true, message: '', trigger: 'change'}],
        mecidalType:[{required: true, message: '就诊类别不能为空', trigger: 'change'}],
        emNum: [
          {required: true, message: '身份证号不能为空', trigger: 'blur'},
          {validator: CheckId, trigger: 'blur'}
          ],
        emType: [{required: true, message: '费用类别不能为空', trigger: 'change'}],
        onSetDate: [{required: true, message: '就诊时间不能为空', trigger: 'change'}],
      },
      userListData: [], // 医生列表
      optionsYuFu:[ // 是否怀孕
        {
          value: 0,
          label: '否'
        },
        {
          value: 1,
          label: '是',
          disabled: true
        }
      ],
       optionsSex:[{
          value: 1,
          label: '男'
       },
       {
          value: 0,
          label: '女'        
       }],
    }
  },
  mounted:function(){
    // 废弃挂号序号 改由后台插入
    // getBillCode({
    //   billCode: '1004'
    // }).then(res=>{
    //   this.formData.registrationNum = res.data.data[0].v_GetBillCode
    //   this.formData.registrationNumMainID = res.data.data[0].v_GetBillID
    // })
  },
  methods:{
    handelRegister(){ // 挂号
      this.$refs['form'].validate((valid)=>{
        if(valid){
          this.handelInsertData()
        }else{
          return
        }
      })
    },
    handelInsertData(){ // 提交数据
      if (!this.formData.makeDate) {
        this.formData.makeDate = dateFmt("yyyy-MM-dd hh:mm:ss",new Date())
      }
      insertPatientInfo(this.formData).then(res=>{
        this.$message.success('挂号成功')
        for(let key in this.formData){
          this.formData[key]=''
        }
        this.formData.maker = sessionStorage.getItem('username')
        this.formData.isYuFu = 0
        this.formData.emSex = 1
        this.$refs['form'].resetFields()
      })
    },
    funSelectUser(){ // 选择医生
      let user = this.userListData.find(item=> item.userCode == this.formData.userCode)
      this.formData.userName = user.userName
    },
    funCheckDept(){ // 检验科室是否选择
      if(this.formData.deptCode == ''){
        this.$message.warning('请先选择就诊科室')
      }
    },
    handelReset(){ // 重置表单
      console.log('reset')
    },
    sexChange(item){ // 性别更改 用于禁用怀孕项
      if(item == 0){
        this.optionsYuFu[1].disabled = false
      }else{
        this.formData.isYuFu = 0
        this.optionsYuFu[1].disabled = true
      }
    },
    queryUserList(queryString,cb){ // 查询医生列表
      if(this.formData.deptCode == ''){
          this.$message.warning('请先选择就诊科室')
          cb([])
      }else{
        getGyUser({deptCode: this.formData.deptCode}).then(res=>{
          let arr=[]
          if(res.data.code == '1'){
            this.$message.warning('科室没有医生')
            cb([])
          }else{
            res.data.data.forEach(element=>{
              arr.push({value: element.userName,id: element.userCode})
            })
            cb(arr)
          }
        })
      }

    },
    handleSelect(item){ // 选择医生
      this.formData.userCode = item.id
    },
    selectDept(item){
      this.optionsDeptName.forEach(element => {
        if(element.deptCode==item){
          this.formData.deptName = element.deptName
        }
      })
       getGyUser({deptCode: this.formData.deptCode}).then(res=>{
         if(res.data.code == '0'){
           this.userListData = res.data.data
         }else{
           this.$message.warning('科室没有医生')
           this.userListData = []
         }
       })
    }
  }
}
</script>
<style scoped>
.huanzhe{
  padding-top: 24px;
}
.el-select,.el-date-editor{
  width: 100%;
}
/* .huanzhe .el-input__inner{
  width: 240px;
}  */
input{
  min-width: 480px !important;
}
.huanzhe-btn{
  margin-top: 24px;
}
.huanzhe-btn button{
  display: block;
  margin: 0 auto;
}
</style>




