<template>
  <!-- 患者列表组件 -->
  <section>
    <div class="table-top-searchbox">
      <!-- 顶部查询框 -->
      <el-input suffix-icon="el-icon-search" clearable size="small"
      placeholder="输入您想要查询的病人姓名/就诊号/身份证号" v-model="tableArgs.searchText"
      @keydown.enter.native="getData"  @blur="getData" ></el-input>
    </div>
    <el-table :data="tableData" fit border v-loading="loading">
      <el-table-column prop="registrationNum" min-width="120" label="挂号序号"></el-table-column>
      <el-table-column prop="mecidalNum" min-width="110"  label="就诊号"></el-table-column>
      <el-table-column prop="emName" min-width="100" label="姓名"></el-table-column>
      <el-table-column prop="onSetDate" min-width="160" label="就诊日期"></el-table-column>
      <el-table-column prop="userName" min-width="100" label="开单医生"></el-table-column>
      <el-table-column prop="deptName" min-width="100" label="开单科室"></el-table-column>
      <el-table-column prop="treatmentState" min-width="70" label="就诊状态"></el-table-column>
      <el-table-column prop="date" label="患者信息">
        <template slot-scope="scope">
          <span class="link-blue" @click="openPatientInfo(scope.row.registrationNum)">患者信息</span>
        </template>
      </el-table-column>
      <el-table-column prop="date" label="重新就诊" v-if="active == 'not'">
        <template slot-scope="scope">
          <span class="link-blue" @click="openTreatment(scope.row)">重新就诊</span>
        </template>
      </el-table-column>
      <el-table-column prop="date" v-if="op" label="操作" align="center">
        <template slot-scope="scope">
          <span class="link-blue" v-if="active == 'already'" @click="openTreatment(scope.row)">马上就诊</span>
          <span class="link-blue" v-if="active == 'not'" @click="openCaseInfo(scope.row)">查看病例</span>
        </template>
      </el-table-column>
    </el-table>
    <div :span="24" class="toolbar">
      <el-pagination background layout="total, prev, pager, next, jumper" @current-change="handlePageChange" :current-page="tableArgs.pageNumber" :page-size="tableArgs.pageSize" :total="total" style="float:right;">
      </el-pagination>
    </div>
    <el-dialog
      :visible.sync="infoVisible"
      title="患者信息"
      align="center"
      width="70%">
      <patient-info style="padding: 0 12px;" ref="child" @childClose="handelCloseDialogInfo" :uid="cuid"></patient-info>
    </el-dialog>
  </section>
</template>
<script>
import {
  getSelectPatientListTable,
  getSelectPatientInfoTable
} from "../api"
import {mapMutations} from 'vuex'
import patient_info from './patient_info_window'
export default {
  components:{
    'patient-info': patient_info
  },
  props:{
    op:{ // 是否显示操作栏
      default: true
    },
    active:{type: String}
  },
  data(){
    return{
      tableData: [], // 患者列表数据
      total: 0, // 数据条数
      infoVisible: false, // 患者弹窗显示
      loading: true, // 显示加载
      cuid: '', // 子组件用于获取患者信息
      tableArgs:{ // 查询参数
        searchText:'',
        sortName: 'makeDate',
        sortOrder: 'desc',
        pageSize: 10,
        pageNumber: 1,
        deptcode: '',
        treatmentState: 0,
      }
    }
  },
  methods:{
    ...mapMutations('patient',['getPatientInfo','showpatientInfo']),
    getData(prop){ // 获取数据
      this.loading = true
      prop = prop || {}
      if(prop.index){ // 页码重置
        this.tableArgs.pageNumber=prop.index
      }
      Object.assign(this.tableArgs,prop) // 合并对象
      this.tableArgs.deptcode = sessionStorage.getItem('current_deptCode')
      getSelectPatientListTable(this.tableArgs).then(res=>{
        this.loading = false
        if(res.data.code== '0'){
          this.total = res.data.data.total
          this.tableData = res.data.data.content
        }else{
          this.total = 0
          this.tableData = []
        }
      }).catch( err => {
        this.loading = false
      })
    },
    handlePageChange(index){ // 页码切换
      this.tableArgs.pageNumber = index
      this.getData()
    },
    openTreatment(row){ // 进入治疗页面
        sessionStorage.setItem('current_registrationNum',row.registrationNum)
        this.showpatientInfo()
        let url =  "/#/"+ this.$route.path.split('/')[1]+ "/treatment"
        let fun = function(){ // 回调方式页面跳转
          window.location  = url
        }
        this.getPatientInfo({ // store存储
          id: row.registrationNum,
          cb: fun
        })
    },
    openCaseInfo(row){ // 查看病例
        sessionStorage.setItem('current_registrationNum',row.registrationNum)
        this.showpatientInfo()
        let url =  "/#/"+ this.$route.path.split('/')[1]+ "/case_info?id=" + row.registrationNum + "&mn=" + row.mecidalNum
        let fun = function(){ // 回调方式页面跳转
          window.location  = url
        }
        this.getPatientInfo({ // store存储
          id: row.registrationNum,
          cb: fun
        })
     // this.$router.push({path:'case_info',query:{id:registrationNum}})
    },
    openPatientInfo(registrationNum){ // 查看患者详情
      this.cuid = registrationNum
      this.infoVisible = true
      this.$nextTick(()=>{
        this.$refs['child'].getData()
      })
      return
      this.$router.push({path:'patient_info',query:{id:registrationNum}})
    },
    handelCloseDialogInfo(){
      this.infoVisible = false;
    }
  }
}
</script>
<style scoped>
.link-blue{
  text-align: center;
  color: #3E85FA;
  cursor: pointer;
}
.table-top-searchbox{
  position: absolute;
  top: 0;
  right: 2px;
  height: 60px;
  width: 360px;
  font-size: 18px;
  z-index: 10;
}
</style>




